@import './../../styles/global.scss';

.slider {
  overflow: hidden;
  display: -webkit-box;
  display: -ms-flexbox;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  position: relative;
  max-height: 495px;
  height: 100%;
  width: 100%;
  border-radius: 8.11px;
  &__container {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    height: 100%;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -ms-flex-direction: row;
    flex-direction: row;
    -webkit-transition: -webkit-transform 0.3s ease-in-out;
    transition: -webkit-transform 0.3s ease-in-out;
    -o-transition: transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
  }

  &__image {
    width: 100%;
    max-height: 495px;
    height: 100%;
    object-fit: cover;
    object-position: top;
    display: block;

    &:hover {
      cursor: grab;
    }

    &:active {
      cursor: grabbing;
    }
  }

  &__pagination {
    position: absolute;
    bottom: 24px;
    left: 50%;
    transform: translateX(-50%);
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -ms-flex-direction: row;
    flex-direction: row;
    gap: 6px;

    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;

    &-item {
      width: 6px;
      height: 6px;
      border-radius: 16px;
      cursor: pointer;
      background-color: #9a9a9a;
      -webkit-transition: all 0.3s ease-in-out;
      -o-transition: all 0.3s ease-in-out;
      transition: all 0.3s ease-in-out;
      &_current {
        width: 33px;
        background-color: $mainBlackColor;
      }
    }
  }
}

@media screen and (max-width: 520px) {
  .slider {
    width: auto;
    // min-width: 100%;
    height: 369px;
    border-radius: 0;
    margin: -16px -16px 0;

    &__image {
      min-width: 100%;
      max-width: 100%;
      width: 100%;
      height: 369px;
      object-fit: cover;
    }

    &__pagination {
      bottom: 48px;

      &-item {
        width: 6px;
        height: 6px;
        border-radius: 16px;
        cursor: pointer;
        background-color: #9a9a9a;
        -webkit-transition: all 0.3s ease-in-out;
        -o-transition: all 0.3s ease-in-out;
        transition: all 0.3s ease-in-out;
        &_current {
          width: 33px;
          background-color: $mainBlackColor;
        }
      }
    }
  }
}
